<template>
  <div class="category">
    <van-nav-bar
      title="分类详情"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />

   
<div class="catelist">
    <van-list
      v-model="loading"
      :finished="finished"
      :immediate-check="false"
      finished-text="没有更多了"
      @load="onLoad"
      :offset="10"
    >
       <div class="classification-box">
      <ul class="box-list">
         <li class="list-item" v-for="(item,i) in itemList" :key="i">
            <router-link :to="{ path: '/about', query: { url: item.url} }">
          <span class="item-img">
            <img :src="item.cover">
          </span>
          <p class="item-txt">{{item.name}}</p>
          </router-link>
        </li>
      </ul>
    </div>
    </van-list>

    <!-- <div class="no-data" v-if="!this.itemList">
      <img src="https://img.yzcdn.cn/vant/empty-image-default.png" alt="暂无记录" class="img" />
    </div> -->
    </div>
  </div>
</template>

<script>
import { mhfleiid } from "@/api/api";

export default {
  data() {
    return {
      itemList: [], //数据

      loading: false,
      finished: false,
      // pages:1, //请求第几页
      total: 0, //总共的数据条数
      id: ""
    };
  },
 created(){
    this.pages++;
    this.getroadList();
 },
  methods: {
    getroadList() { 
     this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      // let query = {
      //   id : this.$route.query.id
      // }
    let { id } = this.$route.query;
      mhfleiid(id).then((res) => {
        
        let rows = res.data.list.slice(1); //请求返回当页的列表
        
        this.loading = false;
        this.pages =  res.data.list[0].pages;
        this.total = res.data.list[0].dpages;
        
        
        if (rows == null || rows.length === 0) {
          // 加载结束
          this.finished = true;
          return;
        }

        // 将新数据与老数据进行合并
        this.itemList = this.itemList.concat(rows);

        //  如果列表数据条数>=总条数，不再触发滚动加载
        if (this.itemList.length >= this.total) {
          this.finished = true;
        }
         this.$toast.clear();
      });
    },
    onLoad(){
      // this.pages++;
      this.getroadList();
    },

    back() {
      // this.$router.push({path: "/classify"})
      this.$router.go(-1);
    },
  
  },
};
</script>

<style lang="scss" scoped>
.category {
  .catelist{
    margin-top: 46px;
  }
  .classification-box {
    width: 100%;
    margin-top: 46px;

    .box-list {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      padding: 0 14px;
      padding-top: 14px;
      background-color: #fff;
      margin-bottom: 10px;

      .list-item {
        width: 108px;
        border-radius: 6px;
        box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.1);
        overflow: hidden;
        margin-bottom: 14px;

        .item-img {
          display: block;
          width: 108px;
          height: 136px;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .item-txt {
          margin-top: 6px;
          width: 100%;
          text-overflow: ellipsis;
          min-height: 30px;
          font-size: 14px;
          line-height: 20px;
          color: #333;
          text-align: center;
        }
      }
    }
  }

  /deep/ .van-nav-bar .van-icon {
    color: #49dda1;
  }
  /deep/.van-nav-bar__text {
    color: #49dda1;
  }
}
</style>